.home_container{
    row-gap: 7rem;
}

.home_content {
  grid-template-columns: 100px 1.5fr 1fr;
  padding-top: 5.5rem;
  column-gap: 2rem;
  align-items: center;
}
.word{
    
    font-size: var(--h2-font-size);
}

.firstLetter{
    font-size: var(--h1-font-size);
}


.home_title{
    /* font-size: 2.75rem; */
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}

.home_hand{
    width: 24px;
    height: 24px;
    margin-left: 0.4rem;
}

.home_subtitle{
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 6.9rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);
}

.home_subtitle:before{
    content: '';
    position: absolute;
    width: 90px;
    height: 1.5px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;
}

.home_description{
    max-width: 400px;
    margin-bottom: var(--mb-3)
}

.home_img {
  background: url(../../assets/ismeet.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 0 9px rgba(247, 240, 240, 0.5);
  order: 1;
  justify-self: center;
  width: 300px;
  height: 300px;
  animation: profile_animate 7s ease-in-out infinite 1s;
}

@keyframes profile_animate {
    0%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50%{
        border-radius: 35% 70% 60% 40%/70% 45% 60% 30%;
    }
    100%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    
}

.home_social{
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home_social_icon{
    font-size: 1.5rem;
    color: var(--title-color);

}

.home_social_icon:hover{
    color: var(--title-color-dark);
    
}

.home_scroll{
    margin-left: 9.5rem;

}

.wheel{
    animation: scroll 2s ease infinite;
}


@keyframes scroll {
    0%{
       transform: translateY(0); 
    }
    30%{
       transform: translateY(3.75rem); 
    }
   
}

.home_scroll_name{
    color: var(--title-color);
    font-weight: var(---font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;

}

.home_scroll_arrow{
    font-size: 1.25rem;
    color: var(--title-color);
    
}

  /* breakpoints */

  /* large devices */
  @media screen and (max-width: 992px) {
    .home_content{
        grid-template-columns: 100px, repeat(2, 1fr);
        column-gap: 1.25rem;
    }

    .home_hand{
        width: 24px;
        height: 24px;
    }

    .home_subtitle{
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);
    }

    .home_subtitle:before{
        width: 42px;
        top: 0.8rem;
    }

    .home_description{
        max-width: initial;
        margin-bottom: var(--mb-2-5);
    }
    .home_img{
        width: 250px;
        height: 250px;
        justify-self: center;
        box-shadow: inset 0 0 0 7px rgba(247, 240, 240, 0.5);
    }
    .home_scroll{
        margin-left:7.5rem;
    }
    
  }
  /* medium devices */
    @media screen and (max-width: 768px) {
            .home_content{
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
        column-gap: 1rem;
    }
    .home_img{
        order: initial;
        justify-self: center;

    }
    .home_data{
        grid-column: 1/3;
        text-align: center;
    }

    .home_img{
        width: 200px;
        height: 200px;
        box-shadow: inset 0 0 0 5px rgba(247, 240, 240, 0.5);
    }
    .home_scroll{
        display: none;
    }
    
    
  }

    @media screen and (max-width: 576px) {
    
  }
  /* small devices */
    @media screen and (max-width: 350px) {
        .home_img{
            width: 180px;
            height: 180px;
            justify-self: center;
        }
        .home_hand{
            width: 22px;
            height: 22px;
        }
  }
