/* GOOGLE FONTS */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&family=Varela+Round&display=swap');

:root{
  --header-height: 3rem;

  /* colors */
  --hue:0;
  --sat:0%;
  --title-color: hsl(var(--hue),var(--sat),30%);
  --title-color-dark: hsl(var(--hue),var(--sat),0%);
  --text-color:hsl(var(--hue),var(--sat),46%);
  --body-color:hsl(var(--hue),var(--sat),98%);
  --container-color: #fff;

  /* Font */
  --body-font: 'Poppins', sans-serif;

  /* font-size */
  --big-font-size: 3.1rem;
  --h1-font-size: 2.25rem;
  --h2-font-size:1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.725rem;

  /* font weight */
  --font-normal:400;
  --font-medium:500;
  --font-semi-bold: 600;

  /* margin bottom */
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /* z-index */
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-model: 1000;
}
  /* responsive  */
  @media screen and (max-width: 992px){
    :root{
      /* font-size */
  --big-font-size: 2.5rem;
  --h1-font-size: 1.5rem;
  --h2-font-size:1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.815rem;
  --smaller-font-size: 0.75rem;
  --tiny-font-size: 0.6rem;
    }
  }

  /* base */
  *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  html{
    scroll-behavior: smooth;
  }

  body, button, input, textarea{
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
  }

  body{
    background-color: var(--body-color);
    color: var(--text-color);
  }

  h1,h2,h3{
    color:var(--title-color);
    font-weight: var(--font-semi-bold);
  }

  ul{
    list-style: none;
  }

  a{
    text-decoration: none;
  }

  button{
    cursor: pointer;
    border: none;
    outline: none;
  }

  img{
    max-width: 100%;
    height: auto;
  }

  /* reusable css class */
  .section{
    padding: 6rem 0 2rem;
  }

  .section_title{
    font-size: var(--h1-font-size);
    color: var(--title-color);
    margin-bottom: 4rem;
  }

  .section_subtitle{
    display: block;
    font-size: var(--small-font-size);
    margin-bottom: 3rem;
  }

  .section_title, .section_subtitle{
    text-align: center;
  }


  /* layout */

  .container{
    max-width: 968px;
    margin-left: auto;
    margin-right: auto;
  }

  .grid{
    display: grid;
    gap: 1.5rem;
  }

  /* button */
  .button{
    display: inline-block;
    background-color: var(--title-color);
    color: var(--container-color);
    padding: 1.25rem 2rem;
    border-radius: 1rem;
    font-weight: var(--font-medium);
  }

  .button:hover{
    background-color: var(--title-color-dark);
  }

  .button_icon{
    margin-left: var(--mb-0-5);
  }

  .button_flex{
    display: inline-flex;
    align-items: center;
  }

  /* breakpoints */

  /* large devices */
  @media screen and (max-width: 992px) {
    .container{
      margin-left: var(--mb-1-5);
      margin-right: var(--mb-1-5);

    }
    .button{
      padding: 1rem 1.75rem;
    }
    .button_icon{
      width: 22px;
      height: 22px;
    }
    
    
  }
  /* medium devices */
    @media screen and (max-width: 768px) {
      .section{
        padding: 2rem 0 4rem;
      }
  }

    @media screen and (max-width: 576px) {
            :root{
        --big-font-size:2.4rem;
      }
    
  }
  /* small devices */
    @media screen and (max-width: 350px) {
      :root{
        --big-font-size:2rem;
      }
        .container{
      margin-left: var(--mb-1);
      margin-right: var(--mb-1);

    }

  }